@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --primary-color: #d5bdaf;
  --secondary-color: #e3d5ca;
  --tertiary-color: #f5ebe0;
  --quaternary-color: #d6ccc2;
  --quinary-color: #edede9;
  --primary-text-color: #313131;
}

/*
font-family: 'Montserrat', sans-serif;
font-family: 'Ubuntu', sans-serif;*/



.Navbar_navbar__cxP4I {
  background-color: var(--primary-color);
  color: var(--primary-text-color);
  font-family: "Ubuntu", sans-serif;
  position: absolute;
  top: 0;
  width: 100%;
  height: 120px;
  display: flex;
  align-items: center;
}

.Navbar_list__3HVas {
  display: flex;
  list-style: none;
  justify-content: space-around;
  width: 100%;
  align-items: center;
}

.Navbar_list__3HVas li {
  font-size: 30px;
  cursor: pointer;
  -webkit-user-select: none;
          user-select: none;
  position: relative;
  z-index: 1;
}

.Navbar_overlay__1kEQ1 {
  color: var(--primary-text-color);
  position: relative;
  text-decoration: none;
  width: 100%;
  height: 100%;
}

.Navbar_overlay__1kEQ1::before {
  background: #b8a1e1;
  content: "";
  inset: 0;
  position: absolute;
  opacity: 0;
  -webkit-transform: translate(2px, 2px);
          transform: translate(2px, 2px);

  transition: -webkit-transform 0.4s ease-in-out;

  transition: transform 0.4s ease-in-out;

  transition: transform 0.4s ease-in-out, -webkit-transform 0.4s ease-in-out;
  z-index: -1;
}

.Navbar_overlay__1kEQ1:hover::before {
  opacity: 0.8;
  -webkit-transform: translate(14px, -8px);
          transform: translate(14px, -8px);
}

.Navbar_logo__1pqPS {
  width: 140px;
  cursor: pointer;
  -webkit-user-select: none;
          user-select: none;
  transition: color 1s ease-in-out;
}
.Navbar_logo__1pqPS h2:first-child {
  text-align: left;
}
.Navbar_logo__1pqPS h2:last-child {
  text-align: right;
}
.Navbar_logo__1pqPS h2::first-letter {
  font-size: 120%;
}
.Navbar_logo__1pqPS:hover h2::first-letter {
  color: #b8a1e1;
  transition: 1s ease-in-out;
}
.Navbar_logo__1pqPS h2 > span {
  -webkit-transform: translateY(0%);
          transform: translateY(0%);
  height: 50%;
}
.Navbar_logo__1pqPS:hover h2 > span {
  background-color: darkcyan;
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
  transition:  0.5s ease-in-out;
}
